export const getBgColor = function (status) {
    switch (status) {
        case "created":
        case "":
            return "#73b8fd";
        case "approved":
        case "":
            return "#0080fc";
        case "canceled":
        case "":
            return "#eb004c";
        case "rejected":
        case "":
            return "#740127";
        case "to_confirm_payment":
        case " ":
            return "#9c540d";
        case "success":
        case " ":
            return "#02a38a";
    }
}

export const getLaoStatus = function (status) {
    switch (status) {
        case "created":
            return ""
        case "approved":
            return ""
        case "canceled":
            return ""
        case "rejected":
            return ""
        case "to_confirm_payment":
            return " "
        case "success":
            return " "
        case "cash":
            return ""
        case "bcel":
            return "BCEL"
        default:
            return status;
    }
}

export const getLaoBillingType = function (type) {
    switch (type) {
        case "CustomBill":
            return ""
        case "NewCollectionEvent":
            return ""
        case "FutureInvoice":
            return ""
        case "NewInvoice":
            return ""
        default:
            return type
    }
}

